import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { BookingDataService } from '@services/modules/bookings/dataservices/booking.data.service';
import { UserService } from '@services/modules/user/services/user.service';

@Component({
    selector: 'tz-checkout-processed',
    templateUrl: './checkout-processed.component.html',
    styleUrls: ['./checkout-processed.component.scss'],
})
export class CheckoutSuccessComponent implements OnInit {
    public processedStatus: string;
    public bookingId: string;

    constructor(
        private activeRoute: ActivatedRoute,
        private router: Router,
        public userService: UserService,
        private bookingDataService: BookingDataService
    ) {
        this.processedStatus = this.activeRoute.snapshot.params['status'];
        this.bookingId = this.activeRoute.snapshot.params['bookingId'];
    }

    ngOnInit() {}

    public proceedNext(): void {
        if (this.processedStatus === 'success') {
            this.bookingDataService.setBookings([]);
            if (this.bookingId) {
                this.router.navigate(['bookings', 'booking-summary', this.bookingId]);
            } else {
                // we should not reach that step, but in case....
                this.router.navigate(['bookings']);
            }
        } else {
            this.router.navigate(['checkout'], { replaceUrl: true });
        }
    }
}
